import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import Radium from 'radium';
import * as ServiceApi from '../modules/serviceApi';
import { Link, browserHistory } from 'react-router';

const styles = {
    mainContainer: {
        display: 'flex',
        flexFlow: 'column nowrap',
        justifyContent: 'space-around',
        fontSize: '2vh',
        width: '100vw',
    },
    category: {
        //color: 'rgb(66,139,229)',
        fontWeight: 'bold',
    },
    input: {
        color: 'rgb(91,90,88)',
    },
    responses: {
        display: 'flex',
        padding: '0 5vw 2vh 5vw',
        width: '100vw',
        fontSize: '2.5vh',
        textAlign: 'left',
        color: 'white',
        backgroundColor: 'rgb(66,139,229)',
    },
    textResponse: {
        backgroundColor: 'white',
    },
    textResponseTwo: {
        backgroundColor: 'rgba(211,211,211,0.7)',
    },
    surveyStat: {
        padding: '0 5vw',
    },
    qAndR: {

    },
    surveyName: {
        display: 'flex',
        padding: '2vh 10vw 2vh 10vw',
        width: '100vw',
        fontSize: '2.5vh',
        textAlign: 'center',
        color: 'white',
        backgroundColor: 'rgb(66,139,229)',
    },
    questionBackground: {
        backgroundColor: 'yellow',
        padding: '2vh 2vw',
    },
    questionBackgroundTwo: {
        backgroundColor: 'rgba(66,139,229,.4)',
        padding: '2vh 2vw',
    },
};

class ResponsesView extends Component {
    constructor(props) {
        super(props);
        this.state = {
            responses: [],
            survey: {},
        };
    }

    componentWillMount() {

        const
            survey = this.props.surveys.surveys.find((s) => {
                return s.id === this.props.responses.responses.surveyId;
            });

        this.setState({ survey });
    }

    componentDidMount() {
        const
            answers = this.props.responses.responses.answers,
            survey = this.state.survey,
            surveyQuestionsArray = survey.questions,
            length = answers.length ? `${answers.length / surveyQuestionsArray.length} responses` : 'No repsonses yet';

        answers.forEach((a, idx) => {
            const
                soughtQuestion = surveyQuestionsArray.find((q) => {
                    return a.questionId === q.id;
                }),
                options = soughtQuestion.answer.options,
                index = surveyQuestionsArray.indexOf(soughtQuestion),
                chosenOption = options.find((o) => {
                    console.log(idx);
                    return o.display === a.value;
                }),
                chosenOptionIndex = options.indexOf(chosenOption),
                type = soughtQuestion.answer.type;

            if (type === 'textBox') {
                let
                    responses = soughtQuestion.answer.responses,
                    newAnswer;

                if (responses) {
                    newAnswer = soughtQuestion.answer;
                    newAnswer.responses.push(a.value);
                } else {
                    newAnswer = Object.assign(soughtQuestion.answer, { responses: [] });
                    newAnswer.responses.push(a.value);
                }

                surveyQuestionsArray.splice(index, 1, {
                    answer: newAnswer,
                    id: soughtQuestion.id,
                    text: soughtQuestion.text
                });
            } else {
                let newOptions = options;

                if (newOptions[0].timesSelected >= 0) {
                    newOptions[chosenOptionIndex].timesSelected = newOptions[chosenOptionIndex].timesSelected + 1;
                } else {
                    newOptions = newOptions.map((o) => {
                        return Object.assign(o, { timesSelected: 0 });
                    });
                    newOptions[chosenOptionIndex].timesSelected = newOptions[chosenOptionIndex].timesSelected + 1;
                }

                surveyQuestionsArray.splice(index, 1, {
                    answer: {
                        id: soughtQuestion.answer.id,
                        type: soughtQuestion.answer.type,
                        options: newOptions,
                    },
                    id: soughtQuestion.id,
                    text: soughtQuestion.text
                });
            }
        })
        this.setState({
            survey: {
                id: survey.id,
                name: survey.name,
                preEvent: survey.preEvent,
                questions: surveyQuestionsArray,
            },
            length,
        });
    }

    componentWillReceiveProps() {
    }

    componentDidUpdate() {
        console.log('survey', this.state.survey)
    }

    componentWillUnmount() {
    }

    returnToSurveyOptions() {
        browserHistory.push('/surveyBuilder');
    }

    findSelectedAmount() {
    }

    render() {
        const
            returnToSurveyOptions = this.returnToSurveyOptions.bind(this),
            survey = this.state.survey,
            questions = survey.questions.map((question, idx) => {
                let questionBackground = styles.questionBackground;
                if (idx % 2 === 0) {
                    questionBackground = styles.questionBackgroundTwo;
                }

                if (question.answer.type === 'textBox' && question.answer.responses) {
                    return (
                        <div style={questionBackground}>
                            <div style={styles.category}><span style={styles.input}>text: {question.text}</span></div>
                            <div style={styles.category}><span style={styles.input}>id: {question.id}</span></div>
                            <div style={styles.category}><span style={styles.input}>type: {question.answer.type}</span></div>
                            <div style={styles.category}><span style={styles.input}>Answer id: {question.answer.id}</span></div>
                            <div style={styles.category}>Responses: {
                                question.answer.responses.map((r, idx) => {
                                    let textResponse = styles.textResponse;
                                    if (idx % 2 === 0 ) {
                                        textResponse = styles.textResponseTwo;
                                    }
                                    return (
                                        <div>
                                            <div style={textResponse}>{idx + 1}: {r}</div>
                                        </div>
                                    );
                                })
                            }</div>
                        </div>
                    );
                } else {
                    return (
                        <div style={questionBackground}>
                            <div>text: {question.text}</div>
                            <div>id: {question.id}</div>
                            <div>type: {question.answer.type}</div>
                            <div>Answer id: {question.answer.id}</div>
                            <div>Options:</div>
                            <div>answer choices: {
                                question.answer.options.map((o, idx) => {
                                    return (
                                        <div>
                                            <div>{idx + 1}: {o.display} <span>times selected</span>{o.timesSelected}</div>
                                        </div>
                                    );
                                })
                            }</div>
                        </div>
                    );
                }
            });

        return (
            <div style={styles.mainContainer}>
                <div style={styles.surveyName}>{survey.name}</div>
                <div onClick={returnToSurveyOptions}>Back To Survey Options</div>
                <div style={styles.responses}>{this.state.length}</div>
                <div style={styles.surveyStat}>id: {survey.id}</div>
                <div style={styles.surveyStat}>pre-event: {survey.preEvent}</div>
                <div style={styles.qAndR}>questions and responses:</div>
                {questions}
                <div onClick={returnToSurveyOptions}>Back To Survey Options</div>
            </div>
        );
    }
}

ResponsesView = Radium(ResponsesView);

ResponsesView.propTypes = {
    responses: PropTypes.object,
    admin: PropTypes.object,
    surveys: PropTypes.object,
}

const mapStateToProps = (state) => {
    return {
        admin: state.admin,
        responses: state.responses,
        surveys: state.surveys,
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
    };
};

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(ResponsesView);



