import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import Radium from 'radium';
import { Link, browserHistory } from 'react-router';

const styles = {
    mainContainer: {
        display: 'flex',
        height: '100vh',
        width: '100vw',
        alignItems: 'center',
        cursor: 'pointer',
    },
    header: {
        display: 'flex',
        padding: '2vh 10vw 2vh 10vw',
        width: '100vw',
        fontSize: '2.5vh',
        textAlign: 'center',
        color: 'white',
        backgroundColor: 'rgb(66,139,229)',
        flexFlow: 'row nowrap',
        justifyContent: 'space-around',
    },
    welcome: {
        display: 'flex',
    },
    click: {
        display: 'flex',
        fontSize: '1.75vh',
        alignItems: 'center',
    }
};

class Main extends Component {
    constructor(props) {
        super(props);
        this.state = {
          enabled: true,
        };
    }

    toggleEnabled() {
        this.setState({ enabled: !this.state.enabled });
    }

    componentDidMount() {
    }

    login() {
      browserHistory.push('/login');
    }

    render() {
      const
        login = this.login;
        return (
            <div onClick={login}  style={styles.mainContainer}>
                <div style={styles.header}>
                    <div style={styles.welcome}>Welcome to the MyMatrix Admin App</div>
                    <div style={styles.click}>click to continue</div>
                </div>
            </div>
        );
    }
}
Main = Radium(Main);



Main.propTypes = {
}

export default Main;



